import React from "react";

import Login from "./Login";

import logo from "./img/logo_sababa.png";
import menuIcon from "./img/hamburgermenu.svg";
import closeIcon from "./img/close.svg";

class Header extends React.Component {
  render() {
    return (
      <header className="clearfix">
        <div
          className="hamburger-menu__button"
          onClick={() => this.props.updateStateMenu()}
        >
          <img
            className="hamburger-menu__img"
            src={this.props.isOpen ? closeIcon : menuIcon}
            alt="menu"
          />
        </div>
        <div className="logo-img">
          <img src={logo} width="139px" />
        </div>
        <Login />
      </header>
    );
  }
}

export default Header;
