import React from 'react';
import ReactDOM from 'react-dom';

import Authorization from './Authorization';
import Registration from './Registration';

import userIcon from './img/user.png';

class Login extends React.Component {
    constructor (props) {
        super(props);
        this.state = {
            isAutorization: false,
            isModalLoginOpen: false,
            typeModal: 'авторизация'
        }

        this.openModalLogin = this.openModalLogin.bind(this);
        this.setTypeModal = this.setTypeModal.bind(this);
    }

    openModalLogin() {
        this.setState(state => ({ isModalLoginOpen: !state.isModalLoginOpen}))
    }


    setTypeModal( type ) {
        this.setState({ typeModal: type })
        
    }
    

    render () {
        return (
            <div className="login-wrapper">
                <button className="login-button" onClick={ () => this.openModalLogin()}>
                    <img className="user-icon" src={userIcon} alt="logo"></img>
                    <span className="login-button__text">{this.state.isAutorization ? 'логин' : 'вход'}</span>
                </button>
                
            {this.state.isModalLoginOpen && this.state.typeModal == 'авторизация' &&
                ReactDOM.createPortal(
                        <Authorization openModalLogin={this.openModalLogin}
                                setTypeModal = {this.setTypeModal}/>,
                        document.getElementById('modal'))
                    
            }
            {this.state.isModalLoginOpen && this.state.typeModal == 'регистрация' &&
                ReactDOM.createPortal(
                        <Registration openModalLogin={this.openModalLogin}
                                setTypeModal = {this.setTypeModal}/>,
                        document.getElementById('modal'))
                    
                }
            </div>
            
        )
    }
}

export default Login;