import React from 'react';
import ReactDOM from 'react-dom';

import closeModal from './img/close-modal.svg';
import './styleModalForms.css';

class Authorization extends React.Component {
    constructor(props){
        super(props);
        this.state = {
            login: '',
            password: ''
       }
   }
   
    change = (event) => {
       const { name, value } = event.target;
       this.setState({ [name]: value })
   }

    submit = (event) => {
        event.preventDefault();
        console.log(this.state);
    }

    render () {
        const { login, password } = this.state;
        return (
            <div className="modal" onSubmit = { this.submit }>
                <div className="modal-login-wrapper">
                    <img className="closeModal" src={ closeModal } onClick={ () => this.props.openModalLogin() }/>
                    <div className="type-modal-wrapper">
                        <span className="type-modal active-type" onClick={() => this.props.setTypeModal('авторизация') }>Авторизация</span>
                        <span className="type-modal" onClick={() => this.props.setTypeModal('регистрация') }>Регистрация</span>
                    </div>

                    <form className="login-form">
                        <input  type="text"
                                className="modal-form__input"
                                name="login"
                                value={ login }
                                placeholder="Номер телефона или почта" 
                                onChange={ this.change } />

                        <input  type=""
                                className="modal-form__input"
                                name="password"
                                value={ password }
                                placeholder="Пароль" 
                                onChange={ this.change } />

                        <button className="login-form__btn">Войти</button>
                    </form>

                    <hr color="#fff" width="300px"/>
                        <span className="modal-login__password_recovery"><a>Забыли пароль ?</a></span>
                </div>
            </div>
        )
    }
}

export default Authorization;